}










/* Animation CSS */




body,
button {
  font-family: "Poppins", sans-serif;
}

.container {
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
  transform-style: preserve-3d;
}

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  height: 3rem;
}

.menu {
  max-width: 72rem;
  width: 100%;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}

.logoc {
  font-size: 1.1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  line-height: 4rem;
  color: #555;
}

.logoc span {
  font-weight: 300;
  color: #555;
}

.hamburger-menuc {
  height: 4rem;
  width: 3rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: #555;
}

.barc {
  width: 1.9rem;
  height: 1.5px;
  border-radius: 2px;
  background-color: #555;
  transition: 0.5s;
  position: relative;
}

.barc:before,
.barc:after {
  content: "";
  position: absolute;
  width: inherit;
  height: inherit;
  background-color: #555;
  transition: 0.5s;
}

.barc:before {
  transform: translateY(-9px);
}

.barc:after {
  transform: translateY(9px);
}

.main {
  position: relative;
  width: 100%;
  left: 0;
  z-index: 5;
  overflow: hidden;
  transform-origin: left;
  transform-style: preserve-3d;
  transition: 0.5s;
}

header {
  min-height: 100vh;
  width: 100%;

  position: relative;
}

.overlayc {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color:#e3edf7 !important; 
  display: flex;
  justify-content: center;
  align-items: center;
}

.innerc {
  max-width: 50rem;
  text-align: center;
  color: #fff;
  padding: 0 2rem;
}

.title {
  font-size: 2.7rem;
}

