  justify-content: flex-end;
  color: #555;
}

.barc {
  width: 1.9rem;
  height: 1.5px;
  border-radius: 2px;
  background-color: #555;
  transition: 0.5s;
  position: relative;
}

.barc:before,
.barc:after {
  content: "";
  position: absolute;
  width: inherit;
  height: inherit;
  background-color: #555;
  transition: 0.5s;
}

.barc:before {
  -webkit-transform: translateY(-9px);
          transform: translateY(-9px);
}

.barc:after {
  -webkit-transform: translateY(9px);
          transform: translateY(9px);
}

.main {
  position: relative;
  width: 100%;
  left: 0;
  z-index: 5;
  overflow: hidden;
  -webkit-transform-origin: left;
          transform-origin: left;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: 0.5s;
}

